@import '../styles/variables';
@import '../styles/mixins';
@import '../styles/utility';
@import '../styles/grid';
@import '../styles/buttons';

.secondary-onboarding {
  display: block;
  position: absolute;
  top: 60px;
  padding-bottom: 1px;
  box-shadow: inset 0 -1px 1px 0 $gray-tint-05;
  background-color: $gray-tint-7;
  overflow: hidden;

  #secondaryOnboardingHeader {
    padding: 0 25px 9px;
    background-color: $gray-tint-7;

    .collapseToggleElement {
      z-index: 1;
      width: 93px;
      margin-top: 15px;
      cursor: pointer;

      #collapseToggleArrow, #expandToggleArrow {
        padding-left: 8px;
        padding-top: 8px;
      }
    }

    #progressBar {
      margin-top: 15px;

      #progressBarText {
        float: left;
      }

      #cardProgressCount {
        float: left;
        opacity: 0.5;
        font-size: $font-size-h7;
        color: $gray-shade-6;
        margin: 0 5px;
      }

      #cardProgressBar {
        float: left;
        width: 100px;
        margin-top: 8px;
        height: 6px;
        border-radius: 5px;
        background-color: $gray-tint-1;

        .cardCompletionPercentage {
          float: left;
          height: 6px;
          border-radius: 5px;
          background-color: $green-shade-9;
        }
        &:after {
          background-color: $gray-tint-7;
          clear: left;
        }
      }
    }

    #progressBar-v1 {
      margin: 8px 0 0 30px;

      #headerProgressCircle {
        margin: 0 10px 0 0;
        float: left;
        display: none;
      }
      #progressBarHeader-v1 {
        font-family: Open Sans;
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        margin-top: 8px;
      }
      #progressBarText-v1 {
        font-family: Open Sans;
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 20px;
      }
    }
  }

  .progress-circle {
    border-radius: 1000px;
    border: 8px solid #B9C0C9;
    width: 60px;
    height: 60px;
    margin: 10px;
    background-color: transparent;

    .progressCompletionPercentage {
      float: left;
      position: absolute;
      border-radius: 1000px;
      border: 8px solid  #00A878;
      content: '';
      width: 60px;
      height: 60px;
      margin: -8px 0px 0px -8px;
      background-color: transparent;
    }

    .progressCircleCounter {
      position: absolute;
      font-family: Open Sans;
      font-style: normal;
      font-weight: 600;
      font-size: 12px;
      margin: 13px;
    }

    .progressCircleCompletionIcon {
      position: absolute;
      width: 20px;
      height: 20px;
      margin: 13px;
    }
  }
  .progress-circle-v0 {
    margin: 15px auto 0;
  }

  .carousel {
    overflow: hidden;
    background-color: $gray-tint-7;

    .arrow {
      z-index: 2;
      position: relative;
      float: left;
      margin: 12px 0;
      display: block;
      height: 165px;
      width: 50px;
      border-radius: $border-radius-base;

      #nextArrow-v1 {
        margin-top: 70px;
        margin-left: 115px;
      }

      #nextArrow-v0 {
        margin-top: 70px;
        margin-left: 30px;
      }

      #prevArrow {
        margin-top: 70px;
      }
      &.arrow-left-v0{
        background:linear-gradient(to left, transparent 5%, $gray-tint-7 50%);
      }
      &.arrow-right-v0{
        margin-left: 100px;
        background:linear-gradient(to right, transparent 5%, $gray-tint-7 50%);
      }
    }

    .cards-carousel {
      position: relative;
      z-index: 1;
      overflow: visible;
      float: left;
      width: calc(100% - 200px);

      .cards {
        position: relative;

        .card-v0 {
          float: left;
          width: 300px;
          height: 160px;
          margin: 15px 12px 25px;
          top: 50px;
          left: 25px;
          border-radius: 3px;
          box-shadow: 0 1px 2px 0 $gray-shade-1;
          background-color: $white;

          .card-icon-v0 {
            width: 64px;
            height: 64px;
            display: block;
            margin: 10px auto 0;
          }

          .card-completed-icon-v0 {
            width: 50px;
            height: 50px;
            display: block;
            margin: 30px auto 0;
          }

          .card-header-v0 {
            margin: 5px 0;
            padding: 0 10px;
            font-size: $font-size-base;
            font-weight: $weight-normal;
            text-align: center;
            color: $gray-shade-45;
          }

          .card-completed-header-v0 {
            margin-top: 15px;
            padding: 0 10px;
            font-size: $font-size-sm;
            font-weight: $weight-semibold;
            text-align: center;
            color: $green-shade-9;
          }

          .card-text-v0  {
            margin: 5px 0;
            padding: 0 10px;
            font-size: $font-size-xxs;
            font-weight: $weight-light;
            text-align: center;
            color: $gray-shade-45;
          }
          .card-button {
            display: none;
            width: initial;
            margin: 10px auto 0;
            padding: 9px 20px;
            @include shadow($shadow-z1);
            @include rounded(5px);
          }

          .cardComplete {
            align-items: center;
            padding-top: 35px;
          }

          &:not(.cardComplete):hover {
            margin: 2px;
            width: 320px;
            height: 195px;

            .card-button {
              display: block;
            }
          }
        }

        .card-v1 {
          float: left;
          width: 360px;
          height: 96px;
          margin: 15px 12px 25px;
          top: 50px;
          left: 25px;
          background: #FFFFFF;
          box-shadow: 0px 1px 2px rgba(60, 74, 84, 0.3);
          border-radius: 4px;
          display: flex;
          flex-wrap: wrap;
          flex-direction: column;
          justify-content: center;
          align-items: flex-start;

          .card-icon-v1 {
            width: 64px;
            height: 64px;
            margin: 20px;
          }

          .card-header-v1 {
            font-family: Open Sans;
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 20px;
            color: #1D3049;
            width: 300px;
          }

          .card-text-v1 {
            font-family: Open Sans;
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 16px;
            color: #41536B;
            width: 250px;
          }

          .card-button-v1 {
            font-family: Open Sans;
            font-style: normal;
            font-weight: 600;
            font-size: 13px;
            line-height: 19px;
            color: #3B70D4;
          }

          &:not(.cardComplete):hover {
            transform: scale(1.10);
            padding: 1px;
          }

          .card-completed-icon-v1 {
            color: #00A878;
            font-weight: 600;
            font-size: 13px;
            padding-left: 15px;
            background: url('../images/SecondaryOnboarding/circle-success-small.svg') no-repeat;
            background-position-y: center;
          }
        }
      }
    }
  }
}

//Overrides
.empty .secondary-onboarding-overrides {
  top: 280px;
}

.vaultContent .secondary-onboarding-overrides {

  #options {
    top: 300px;
  }

  &.collapsed {
    #options {
      top: 120px;
    }
  }

  .emptyVaultContainer {
    top: 0px;
  }
}

.secondary-onboarding-overrides {
  .middlePosition {
    top: 300px;
  }

  #tools, #ad {
    &.middlePosition {
      top: 60px;
    }
  }

  .bottom {
    top: 295px;
  }

  .secondary-onboarding-bottom {
    top: 355px;
  }

  .emptyVault {
    top: -155px;
    overflow: hidden;
  }

  #emptySites,
  #emptyNotes,
  #emptyFormFills,
  #emptySentShares,
  #emptySharedFolders,
  #emptyReceivedShares,
  #emptyEmergencyTrusted,
  #emptyEmergencyTrusting,
  #emptyCreditMonitoring
  {
    .emptyVault {
      top: 0;

      .tour {
        margin-top: 0;
        top: 0;
      }
    }
  }

  #sentSharesContent, #sharedFoldersContent, #receivedSharesContent  {
    position: relative;
    top: 220px;
  }

  .emptyVaultContainer {
      top: 200px;
  }

  #options {
    top: 360px;
  }

  #emergencyTrusted, #emergencyTrusting {
    top: 235px;
    position: relative;
  }

  #emptyEmergencyTrustingOmaria {
    top: 340px;
  }

  &.collapsed {

    #secondaryOnboardingContainer {
      box-shadow: inset 0 -1px 1px 0 $gray-tint-05;
      background-color: $gray-tint-7;
    }

    #main {
      top: 60px;
    }

    #mainScroll {
      top: 115px;
    }

    #options {
      top: 160px;
    }

    #sharingMenu, #emergencyAccessMenu {
      top: 100px;
    }

    #sentSharesContent, #sharedFoldersContent, #receivedSharesContent {
      position: relative;
      top: 40px;
    }

    #emergencyTrusted, #emergencyTrusting {
      top: 55px;
      position: relative;
    }

    #emptyEmergencyTrustingOmaria {
      top: 285px;
    }

    .emptyVaultContainer {
      top: 180px;
    }
  }
}
